
























import Vue from "vue";
import Component from "vue-class-component";
import { user } from "@/scripts/user";
import { shh } from "@/scripts/shh";

@Component({})
export default class Signup extends Vue {
  private nick: string = "";
  private head: string = "";

  private get headUrls(): string[] {
    return [
      "/images/head01.jpeg",
      "/images/head02.jpeg",
      "/images/head03.jpeg",
      "/images/head04.jpeg",
      "/images/head05.jpeg",
      "/images/head06.jpeg",
      "/images/head07.jpeg",
      "/images/head08.jpeg",
      "/images/head09.jpeg",
      "/images/head10.jpeg"
    ];
  }

  private get ready(): boolean {
    return this.nick !== "" && this.head !== "";
  }

  private selectHead(head: string) {
    this.head = head;
  }

  private async sure() {
    await shh.initWeb3()
    const keyPair: string = await shh.newKeyPair();
    const u = new user.User(keyPair, this.nick, this.head);
    u.save();
    this.$router.push({ path: "/" });
  }
}
